body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.dashboard {
  position: relative;
  padding: 20px;
  background-color: #282c34;
  min-height: 100vh;
  font-size: calc(10px + 2vmin);
  color: white;
}
.dashboard__container {
  position: relative;
/*  overflow: hidden;*/
  padding: 15px 15px 0 15px;
  margin-top: 20px;
  min-height: 200px;
  height: auto;
  background: #ffffff;
}
.project {
  height: 80px;
  box-shadow: 0 0 10px black;
  margin-bottom: 15px;
}
.project_info {
  color: #444444;
  height: 100%;
  padding: 10px 0;
  text-align: center;
  font-size: 12px;
}
.project_comment_container {
  position: absolute;
  right: -110%;
  width: 250px;
  padding-top: 15px;
  top: 0;
  border: 1px solid #ffffff;
  background: #ffffff;
  box-shadow: 0 0 10px black;
  z-index: 999;
}
.project_comment {
  font-size: 0.8em;
}
